import Jwt from 'jwt-network';

const {JwtNetWork, JwtToken} = Jwt;

const token = new JwtToken();

const props = {
    contextUrl: 'http://43.226.144.231:3000/',
    jwtToken: token
};

const network = new JwtNetWork(props);

/**
 * 可以多加一层
 *todo
 * 如  404 403等通用拦截
 */

export const auth = user => network.fetchGet("/users/common/auth", user).then(rs => {
    const {msg, success} = rs;
    if (success) {
        token.setJwtToken(msg);
        return true;
    } else {
        return false;
    }
});

/**
 * 注册方法
 * @param user
 * @returns {*|Promise<any | never>}
 */
export const register = user => network.fetchJson("/common/register", user);

export const profile = () => network.fetchGet("/user/profile");
