import React from 'react';
import {Form, Input, Button, message, Card, Alert, Typography} from 'antd';
import {auth} from "../../network/network";

import LayoutConfig from './layoutConfig';

const {layout, tailLayout} = LayoutConfig;

const {Text} = Typography;

const Title = <Alert
    message={<Text level={2} type="secondary">请先登录</Text>}
    description={<><Text type="secondary">欢迎登录，请填写账号密码，</Text><br/>
        <Text type="secondary">网站将对所填写的个人信息以及所授权的社交账号信息严格保密 :)</Text></>}
    type="info"
/>;

const Login = props => {


    const onFinish = values => {
        /*auth(values).then(r => {
            if (r) {
                message.success("aaa", 2)
                    .then(_r => {
                        //跳转到首页
                        console.info(_r);
                    })
            } else {
                message.error('fail')
            }
        })*/
    };

    const onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };

    const toReg = () => props.history.push('/reg');

    return (
        <Card title={Title}>
            <Form
                {...layout}
                initialValues={{
                    remember: true,
                }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Form.Item
                    label="用户名"
                    name="username"
                    rules={[
                        {
                            required: true,
                            message: '用户名为必填信息！',
                        },
                    ]}
                >
                    <Input/>
                </Form.Item>

                <Form.Item
                    label="登录密码"
                    name="password"
                    rules={[
                        {
                            required: true,
                            message: '登录密码是必填信息!',
                        },
                    ]}
                >
                    <Input.Password/>
                </Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit">
                        登录
                    </Button>
                    <Button onClick={toReg}>
                        先注册
                    </Button>
                </Form.Item>
            </Form>
        </Card>
    );
};
export default Login
