import React from 'react';
import {Carousel} from "antd";
import {CoolCard, CoolCardImage, CoolCardText} from "react-cool-card";

const Carouser = () => <Carousel autoplay effect="fade" className='carousel'>
    <CoolCard width={'70%'}>
        <CoolCardImage src="https://iph.href.lu/1024x500?text=%E5%8D%A0%E4%BD%8D1" alt="sample"/>
        <CoolCardText
            title="This is a title."
            description="This is a description."
        />
    </CoolCard><CoolCard width={'70%'}>
    <CoolCardImage src="https://iph.href.lu/1024x500?text=%E5%8D%A0%E4%BD%8D2" alt="sample"/>
    <CoolCardText
        title="This is a title."
        description="This is a description."
    />
</CoolCard><CoolCard width={'70%'}>
    <CoolCardImage src="https://iph.href.lu/1024x500?text=%E5%8D%A0%E4%BD%8D3" alt="sample"/>
    <CoolCardText
        title="This is a title."
        description="This is a description."
    />
</CoolCard>
</Carousel>;

export default Carouser;
