import React from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.min.css';
import './static/css/app.css';

import {HashRouter as Router, Route} from "react-router-dom";


import Header from "./component/header/header";
import Index from "./component/index/indexPage";
import Record from "./component/record/recordPage";
import Vip from "./component/vip/vipPage";
import Personal from "./component/personal/personalPage";
import Support from "./component/support/supportPage";
import RecodeInfo from "./component/record/finished/recodeInfo";
import Login from "./component/auth/login";
import Reg from "./component/auth/reg";
import Footer from "./component/footer/footer";

const App = () => <React.Fragment>
    <div className="site-card-wrapper container" style={{minHeight: document.body.offsetHeight-160}}>
        <Router>
            <Header/>
            <div style={{height: 60}}/>
            <Route exact path="/" component={Index}/>
            <Route path="/record" component={Record}/>
            <Route path="/vip" component={Vip}/>
            <Route path="/personal" component={Personal}/>
            <Route path="/support" component={Support}/>
            <Route path='/recodeInfo' component={RecodeInfo}/>

            <Route path='/login' component={Login}/>
            <Route path='/reg' component={Reg}/>
        </Router>
    </div>
    <div style={{height: 30}}/>
    <Footer/>
</React.Fragment>;

export default App;
