import React, {useState} from 'react';
import {Form, Input, Button, Card, Alert, Typography, Spin, Modal} from 'antd';

import LayoutConfig from './layoutConfig';
import {register} from "../../network/network";

const {Text} = Typography;

const {layout} = LayoutConfig;

const Title = <Alert
    message={<Text level={2} type="secondary">请先注册</Text>}
    description={<><Text type="secondary">欢迎注册，请填写表单信息，</Text><br/>
        <Text type="secondary">网站将对所填写的个人信息以及所授权的社交账号信息严格保密 :)</Text></>}
    type="info"
/>;

const Reg = props => {

    const [isLoading, setLoading] = useState(false);

    const onFinish = val => {
        delete val.rePass;
        setLoading(true);
        register(val.user).then(rs => {
            setLoading(false);
            const {success, msg} = rs;
            if (success) {
                Modal.success({
                    content: '注册成功，请先登录！',
                    onOk: () => props.history.push('/login')
                });
            } else {
                Modal.error({
                    content: typeof msg === 'string' ? msg : '服务器内部错误，请联系管理员！'
                });
            }
        });
    };

    return (
        <Spin spinning={isLoading} tip="提交中...">
            <Card title={Title}>
                <Form {...layout} onFinish={onFinish}>
                    <Form.Item
                        name={['user', 'username']}
                        label="用户名"
                        rules={[
                            {
                                required: true,
                                message: '请输入用户名!',
                            },
                        ]}
                    >
                        <Input/>
                    </Form.Item>

                    <Form.Item
                        name={['user', 'phone']}
                        label="手机号码"
                        rules={[
                            {required: true, message: '请输入您的手机!'},
                            () => ({
                                    validator(rule, value) {
                                        const pattern = /0?(13|14|15|18|17)[0-9]{9}/;
                                        if (!value || pattern.test(value) && value.length === 11) {
                                            return Promise.resolve();
                                        }
                                        return Promise.reject('手机号码格式有误，请重新输入!');
                                    }
                                }
                            )
                        ]}
                    >
                        <Input/>
                    </Form.Item>

                    <Form.Item
                        name={['user', 'password']}
                        label="登录密码"
                        rules={[
                            {
                                required: true,
                                message: '请设置登录密码!',
                            },
                            () => ({
                                validator(rule, value) {
                                    const regExp = /^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){6,19}$/;
                                    if (!value || regExp.test(value)) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject('只能输入长度6-20范围，以字母开头、可带数字、“_”、“.”的字符串!');
                                }
                            })
                        ]}
                        hasFeedback
                    >
                        <Input.Password/>
                    </Form.Item>

                    <Form.Item
                        name="rePass"
                        label="请确认密码"
                        dependencies={['user', 'password']}
                        hasFeedback
                        rules={[
                            {
                                required: true,
                                message: '请输入您的确认密码!',
                            },
                            ({getFieldValue}) => ({
                                validator(rule, value) {
                                    if (!value || getFieldValue(['user', 'password']) === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject('两次输入不一致，请重新确认!');
                                },
                            }),
                        ]}
                    >
                        <Input.Password/>
                    </Form.Item>
                    <Form.Item wrapperCol={{...layout.wrapperCol, offset: 8}}>
                        <Button type="primary" htmlType="submit">
                            立即注册
                        </Button>
                        <Button onClick={() => props.history.push('/login')}>
                            前往登录
                        </Button>
                    </Form.Item>
                </Form>
            </Card>
        </Spin>
    );
};
export default Reg;
