import React from 'react';

import {List, Tabs} from 'antd';
import Finished from "./finished/finished";

const { TabPane } = Tabs;

const Record=()=><Tabs defaultActiveKey="1" centered>
    <TabPane tab="a" key="1">
        <List dataSource={[]}/>
    </TabPane>
    <TabPane tab="b" key="2">
        <List dataSource={[]}/>
    </TabPane>
    <TabPane tab="c" key="3">
        <Finished/>
    </TabPane>
</Tabs>;

export default Record;
