import React from "react";
import Footer from 'rc-footer';
import 'rc-footer/assets/index.css';

import log from '../../static/img/stock-website-ico.ico'

export default function footer() {

    return <div style={{bottom: "0px"}}><Footer
        columns={[
            {
                icon: (
                    <img alt={'Charming online'} src={log}/>
                ),
                title: 'Charming online',
                url: '#',
                description: 'Charming online',
                openExternal: true
            },
        ]}
        backgroundColor="#343A40"
    />
    </div>
}
