import React from 'react';
import 'bootstrap';
import {withRouter} from "react-router-dom";

import $ from 'jquery';
import {profile} from "../../network/network";

class Header extends React.Component {

    componentDidMount() {
        /*profile().then(rs => {
            const {msg} = rs;
            console.info("======", msg)
        })*/
    }

    toPage = path => {
        this.switchAnimate(() => {
            this.props.history.push(path);
        });
    };

    switchAnimate = (callback) => {
        const $navSwitchBtnDom = $("#nav-switch-btn");
        const $navbarsDom = $("#navbarsExampleDefault");

        $navbarsDom.hide(300, function () {
            $navbarsDom.show();
            $navbarsDom.removeClass("show");
        });

        $navSwitchBtnDom.attr("aria-expanded", false);
        if (callback) {
            callback();
        }
    }

    render() {
        return (
            <nav className="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
                <a className="navbar-brand">Charming online</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse"
                        data-target="#navbarsExampleDefault" id='nav-switch-btn'
                        aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"/>
                </button>

                <div className="collapse navbar-collapse" id="navbarsExampleDefault">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">
                            <a className="nav-link" onClick={() => this.toPage('/')}>Index <span
                                className="sr-only"/></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" onClick={() => this.toPage('/record')}>Record <span
                                className="sr-only"/></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" onClick={() => this.toPage('/vip')}>VIP <span
                                className="sr-only"/></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" onClick={() => this.toPage('/support')}>Support <span
                                className="sr-only"/></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" onClick={() => this.toPage('/personal')}>Personal<span
                                className="sr-only"/></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" onClick={() => this.toPage('/login')}>Login<span
                                className="sr-only"/></a>
                        </li>
                    </ul>
                </div>
            </nav>
        );
    }

}

export default withRouter(Header);
